@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #ffffff;
  --primary-light-gray: #f4f4f4;
  --primary-gray: #ccd1d9;
  --primary-dark-gray: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64,);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-green: green;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}
/*********************** Navbar *************************/

.logo {
  width: 3.25rem;
}

/*-----------------Nav Links --------------------*/

nav a.nav-link {
  font-weight: 200;
  font-size: 1rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1575rem solid rgb(52, 58, 64,);
  border-top: 0.1575rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding:  0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a:active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1575rem solid #f9ab00;
  border-top: 0.1575rem solid var(--primary-orange);
}


/* **************** Header ********************* */

nav {
  z-index: 2;
}

.header-wrapper {
  position:  relative;
  background: url(/RonColeman58/portfolio-project.git/static/media/bluecoats01.966509d0.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}



/* ----------Typed Text -------------------- */

.typed-text {
  font-size: 2rem;
  color: #ffffff;
  color: var(--primary-white);
}

@media(max-width : 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
}

.typed-text {
  font-size: 1.3rem !important;
}


/* ************************ Button offer option ******************* */

.btn-main-offer {
  z-index: 2;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #ffffff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/* *******************Particle Styles************************** */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

@media(max-width: 768px) {
.particles-canvas {
  display: none;
}
}

/*********************** About Me *************************/
.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}


.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 2rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange) ;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/*********************** Services *************************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-gray);
}

.services h1 {
  color: rgb(52, 58, 64,);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.services .circle {
position: relative;
margin: 0.375rem auto;
background: #f9ab00;
background: var(--primary-orange);
border-radius: 50%;
width: 3.125rem;
height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 5px solid #f9ab00;
  border-bottom: 5px solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 0.3125rem rgb(52, 58, 64,);
  border-bottom: 0.3125rem var(--primary-dark);
}

.services .box:hover .icon {
  color: rgb(52, 58, 64,);
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #ffffff;
  color: var(--primary-white);
}

/*********************** Experience *************************/

.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: 0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-gray);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64,);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 00.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  color: #666666;
  color: var(--primary-dark-gray);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

/* ***************************** Media ******************************** */
@media(max-width: 768px) {
  .experience .cotainer {
    padding-left: 0;
  }
}

@media(max-width: 768px) {
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }

  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/* ***************************** Portfolio ******************************** */
.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-gray);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #f9ab00;
  color: var(--primary-orange);
}

.portfolio-image {
  width: 15rem;
  height: 8rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding:  0 0.5;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

.image-box-wrapper {
 display: -webkit-flex;
 display: flex;
 -webkit-flex-wrap: wrap;
         flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

/* ***************************** Overflow Box ******************************** */
.overflow {
  position: absolute;
  opacity: 1;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
}

/* ***************************** Popupbox ******************************** */
.portfolio-image-popupbox {
  width: 22rem;
  padding: 0 0.5;
}

.popupbox-wrapper {
  margin-top:  7rem;
}

.hyper-link {
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}

.hyper-link:hover { 
  color: #ef4035; 
  color: var(--primary-red);
}

/* ***************************** Popup Media ******************************** */

@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

 @media(max-height: 570px) {
  .popupbox-wrapper {
    height: 90%;
  }

  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }

  .portfolio-image-popupbox {
    width: 30%;
  }
}

/* ***************************** Testimonials ******************************** */

.testimonials {
  background-image: url(/RonColeman58/portfolio-project.git/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgba(0,0,0,0.7);
  background: var(--primary-transparent-black);
}

.myCarousel {
  color: #ffffff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel {
  color: #f9ab00;
  color: var(--primary-orange);
  font-weight: 600;
  text-transform: uppercase;  
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel ,slide {
  background: transparent !important;
  height: 100%;
  padding-top: 2rem;
}

.carousel-root  {
  margin: auto !important;
  margin-top: 3% impo !important;
}

/* ***************************** Contacts ******************************** */
.contacts {
  background: #000000;
  background: var(--primary-black);
  padding: 3rem;
}

.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-gray);
  font-size: 1.2rem;
}

/* ***************************** Inouts ******************************** */
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background:  none;
  resize: none;
  border: 0;
  border-bottom: 0.125px solid #666666;
  border-bottom: 0.125px solid var(--primary-dark-gray);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125px solid #666666;
  border-bottom: 0.125px solid var(--primary-dark-gray);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contacts-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125PX;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
width: 100%;
transition: width 0.3s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}
/* --------------------Input errors------------------------ */

.error-message {
  color: #ffffff;
  color: var(--primary-white);
  background: #ef4035;
  background: var(--primary-red);
  text-align: left;
}

.success-message {}

/* ***********************Media for Imput ****************************** */
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }
  .contacts p {
    padding: 0.4rem;
  }

}

/* **************** Footer Component ********************* */
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #f4f4f4;
  color: var(--primary-light-gray);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-gray);
}

.footer a:hover {
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}
